				</div>
			</div>
		</div>
		<div class="company_info">
			<div class="form flex_between">
				<div class="form_list flex_start">
					<div class="form_list_left">
						合伙企业
					</div>
					<div class="form_list_right">
						<el-input v-model="fundInfo.name" ref="input1" :disabled="isDisabledEditable" placeholder="请输入内容" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						基金期限（年）
					</div>
					<div class="form_list_right fund_limit_time flex_between">
						<el-input :disabled="isDisabledEditable" v-model="fundInfo.fund_expire[0]" placeholder="" clearable>
						</el-input>
						<span>+</span>
						<el-input :disabled="isDisabledEditable" v-model="fundInfo.fund_expire[1]" placeholder="" clearable>
						</el-input>
						<span>+</span>
						<el-input :disabled="isDisabledEditable" v-model="fundInfo.fund_expire[2]" placeholder="" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						基金简称
					</div>
					<div class="form_list_right">
						<el-input v-model="fundInfo.short_name" :disabled="isDisabledEditable" placeholder="请输入内容" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						协议到期日
					</div>
					<div class="form_list_right">
						<el-date-picker v-model="fundInfo.agreement_date" :disabled="isDisabledEditable" type="date" placeholder="选择日期" format="yyyy年MM月dd日" value-format="yyyy-MM-dd" :editable='false'>
	    					</el-date-picker>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						管理公司全称
					</div>
					<div class="form_list_right">
						<el-input  v-model="fundInfo.company" placeholder="请输入内容" clearable :disabled="isDisabledEditable">
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						基金退出状态
					</div>
					<div class="form_list_right">
						<el-input v-model="fundInfo.fund_status" disabled placeholder="请输入内容" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						工商设立日期
					</div>
					<div class="form_list_right">
						<el-date-picker v-model="fundInfo.industry_date" :disabled="isDisabledEditable" type="date" placeholder="选择日期" format="yyyy年MM月dd日" value-format="yyyy-MM-dd" :editable='false'>
	    					</el-date-picker>
					</div>
				</div>
				<!--<div class="form_list flex_start">
					<div class="form_list_left">
						认购基金
					</div>
					<div class="form_list_right">
						<el-select v-model="fundInfo.subscribe_fund" multiple collapse-tags :disabled="isDisabledEditable" placeholder="请选择">
						    <el-option-group v-for="group in allSubscriptionFundList" :key="group.label" :label="group.label"> 
								<el-option v-for="item in group.options" :key="item.id" :label="item.name" :value="item.id">
								</el-option>
							</el-option-group>
						</el-select>
					</div>
				</div>-->
				<div class="form_list flex_start">
					<div class="form_list_left">
						币种
					</div>
					<div class="form_list_right">
						<el-select v-model="fundInfo.unit" :disabled="isDisabledEditable" clearable placeholder="请选择">
						    <el-option v-for="item in coinList" :key="item.value" :label="item.label" :value="item.value">
						    </el-option>
						</el-select>
					</div>
				</div>
				<!--<div class="form_list flex_start">
					<div class="form_list_left">
						认购额
					</div>
					<div class="form_list_right">
						<el-input v-model="fundInfo.rengou_fee" :disabled="isDisabledEditable" placeholder="请输入内容" clearable>
						</el-input>
					</div>
				</div>-->
				<div class="form_list flex_start">
					<div class="form_list_left">
						基金规模（万元）
					</div>
					<div class="form_list_right">
						<el-input v-model="fundInfo.fund_scale" :disabled="isDisabledEditable" placeholder="请输入内容" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						负责合伙人
					</div>
					<div class="form_list_right">
						<el-select v-model="fundInfo.response_partner_id" :disabled="isDisabledEditable" filterable clearable placeholder="请选择">
						    <el-option v-for="item in allUserList" :key="item.id" :label="item.name" :value="item.id">
						    </el-option>
						</el-select>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						投资项目总数
					</div>
					<div class="form_list_right">
						<el-input v-model="fundInfo.company_number" :disabled="true" placeholder="请输入内容" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						投研经理
					</div>
					<div class="form_list_right">
						<el-select v-model="fundInfo.manager_id" :disabled="isDisabledEditable" filterable clearable placeholder="请选择">
						    <el-option v-for="item in allUserList" :key="item.id" :label="item.name" :value="item.id">
						    </el-option>
						</el-select>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						当前倍数
					</div>
					<div class="form_list_right">
						<el-input v-model="fundInfo.current_mult" :disabled="true" placeholder="请输入内容" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						数据创建人
					</div>
					<div class="form_list_right">
						<el-select v-model="fundInfo.creater_id" :disabled="isDisabledEditable" clearable filterable placeholder="请选择">
						    <el-option v-for="item in allUserList" :key="item.id" :label="item.name" :value="item.id">
						    </el-option>
						</el-select>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						复合倍数
					</div>
					<div class="form_list_right">
						<el-input v-model="fundInfo.much_multiple" :disabled="isDisabledEditable" placeholder="请输入内容" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						已投资总额（万元）
					</div>
					<div class="form_list_right">
						<el-input v-model="fundInfo.has_invest" :disabled="true" placeholder="请输入内容" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						备案状态
					</div>
					<div class="form_list_right">
						<el-select v-model="fundInfo.filing_status" :disabled="isDisabledEditable" clearable placeholder="请选择">
						    <el-option v-for="item in allFilingStatus" :key="item.id" :label="item.name" :value="item.id">
						    </el-option>
						</el-select>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						私募基金备案编码
					</div>
					<div class="form_list_right">
						<el-input v-model="fundInfo.filing_number" :disabled="isDisabledEditable" placeholder="请输入内容" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						统一社会信用代码
					</div>
					<div class="form_list_right">
						<el-input v-model="fundInfo.creditcode" :disabled="true" placeholder="请输入内容" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						是否隐藏
					</div>
					<div class="form_list_right flex_start">
						<div @click="changeHideStatus(1)" class="radio_btn cursor" :class="[isHide?'radio_btn_active':'']">
							是
						</div>
						<div @click="changeHideStatus(2)" class="radio_btn cursor" :class="[!isHide?'radio_btn_active':'']">
							否
						</div>
					</div>
				</div>
			</div>
			<!--表单按钮模块-->
			<div v-if="!isDisabledEditable" class="form_submit flex_end">
				<button @click="cancelEdit">取消</button>
				<button @click="saveSubmit">保存</button>
			</div>
		</div>
		<!--公司工商信息模块开始-->
		<div class="company_background">
			<div class="block_title flex_between">
				<div class="title_left flex_start">
					<div class="radio"> </div>
					<div class="block_name">
						合伙企业信息
					</div>
				</div>
				<div class="title_right flex_end">
					<!-- <div class="download hover_btn cursor flex_center">
						<span class="iconfont icon-daoru cursor"></span>
						<span>导出股权穿透图</span>
					</div> -->
					<div class="blue_btn download cursor flex_center" @click="downLoadCompanyInfo()" >
						<span class="iconfont icon-daoru cursor"></span>
						<span>导出企业信息</span>
					</div>
				</div>
			</div>
			<company-detail-background v-bind:type="type"></company-detail-background>
		</div>
	</div>
</template>

<script>
	import companyDetailBackground from '../../pages/companyBackground/companyDetailBackground.vue'
	import url from '../../config/url'
	import { dialogConfirm, dialogConfirmHasbtn,isValidNumber,isValidMoneyLength,isValidDecimal } from "../../config/mUtils";
	import { getSubFundDetailApi,getBeiAnStatusApi,getUserlistApi,getSubscriptionFundListApi,saveSubFundInfoApi } from '../../config/api'

	export default {
		inject: ['reload'],
		
		name: 'subFundDetail',
		data() {
			return {
				id: 0,
				fundInfo: {
					fund_expire: ['','',''],
//					subscribe_fund: []
				},
				fundInfoInit: {
					fund_expire: ['','',''],
//					subscribe_fund: []
				},
				isDisabledEditable: true,
				allFilingStatus: [],
				allUserList: [],
				isHide: 1,//表单是否隐藏选项
				allSubscriptionFundList: [
					{
						label: '母基金基金',
						options: []
					},
					{
						label: '自管基金',
						options: []
					}
				],
				// 货币类型
				coinList: [
					{
						value: 1,
						label: '人民币'
					},
					{
						value: 2,
						label: '美元'
					}
				],

				type: 2,//舆情固定参数
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.getBeiAnStatus()
			this.getAllUserList()
			this.getSubscriptionFundList()
			this.getSubFundDetail()
		},
		mounted() {},
		components: {
			companyDetailBackground
		},
		methods: {
			getSubFundDetail(){
				getSubFundDetailApi({
					id: this.id
				}).then(res => {
					if(res.data.msgNo == 1) {
						var fundInfo = res.data.payload;
						fundInfo.fund_expire = this.translateStringToArray(fundInfo.fund_expire);
						
						if(fundInfo.fund_expire.length==1){
							fundInfo.fund_expire[1] = '';
							fundInfo.fund_expire[2] = '';
						}else if(fundInfo.fund_expire.length==2){
							fundInfo.fund_expire[2] = '';
						}
						//console.log(fundInfo.fund_expire);
						this.fundInfoInit = Object.assign({}, fundInfo);
						this.fundInfo = fundInfo;
						this.isHide = this.fundInfo.is_hidden;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			saveSubmit(){
				var is_valid_number = this.isValidFundTime();
				var fundInfo = JSON.parse(JSON.stringify(this.fundInfo));
				var fund_expire = '';
				if (is_valid_number) {
					if(fundInfo.fund_expire[1]==''&&fundInfo.fund_expire[2]==''){
						fund_expire = fundInfo.fund_expire[0];
					}else if(fundInfo.fund_expire[1]==''&&fundInfo.fund_expire[2]!=''){
						dialogConfirm("info", '基金期限不能为空!');
						return;
					}else if(fundInfo.fund_expire[1]!=''&&fundInfo.fund_expire[2]==''){
						fundInfo.fund_expire.splice(2,1);
						fund_expire = fundInfo.fund_expire.join('+');
					}else{
						fund_expire = fundInfo.fund_expire.join('+');
					}
					
				} else {
					return
				}
				//console.log(fund_expire);
				// return false;
				// 判断基金规模表单限制
				if(isValidMoneyLength(parseInt(this.fundInfo.fund_scale))){
					dialogConfirm("info", '基金规模数据过大');
					return
				}
				if(this.fundInfo.fund_scale!=''){
					if(isValidMoneyLength(parseInt(this.fundInfo.fund_scale))){
						dialogConfirm("info", '基金规模数据过大');
						return
					}
					if(!isValidDecimal(this.fundInfo.fund_scale)||parseFloat(this.fundInfo.fund_scale) < 0){
						dialogConfirm('info', '基金规模应大于等于0');
						return
					}
				}
				// if(parseFloat(this.fundInfo.fund_scale) < 0 || (!parseFloat(this.fundInfo.fund_scale))){
				// 	dialogConfirm("info", '基金规模应不小于0');
				// 	return
				// }

//				var subscribe_fund = this.fundInfo.subscribe_fund.join(',');
				var info = {
					much_multiple: this.fundInfo.much_multiple,
					id: this.id,
					short_name: this.fundInfo.short_name,
					name: this.fundInfo.name,
					unit: this.fundInfo.unit,
					industry_date: this.fundInfo.industry_date,
					creditcode: this.fundInfo.creditcode,
					company: this.fundInfo.company,
//					subscribe_fund: subscribe_fund,
					agreement_date: this.fundInfo.agreement_date,
					fund_expire: fund_expire,
					fund_scale: this.fundInfo.fund_scale,
					response_partner: this.fundInfo.response_partner_id,
					manager: this.fundInfo.manager_id,
					creater: this.fundInfo.creater_id,
					filing_status: this.fundInfo.filing_status,
					filing_number: this.fundInfo.filing_number,
					is_hidden: this.isHide,
				}
				saveSubFundInfoApi({
					info: info,
//					subscribe_fund: subscribe_fund
				}).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm("success", res.data.message);
						this.isDisabledEditable = true;
						this.getSubFundDetail()
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 判断基金期限是否有效
			isValidFundTime(){
				if (this.fundInfo.fund_expire[0]) {
					// let is_valid_fund_expire1 = isValidNumber(this.fundInfo.fund_expire[0]);
					// let is_valid_fund_expire2 = isValidNumber(this.fundInfo.fund_expire[1]);
					// if (is_valid_fund_expire1) {
					// 	return true
					// } else{
					// 	dialogConfirm("info", '基金期限输入格式不对');
					// 	return false
					// }
					let is_valid_fund_expire1 = isValidNumber(this.fundInfo.fund_expire[0]);
					let is_valid_fund_expire2 = isValidNumber(this.fundInfo.fund_expire[1]);
					let is_valid_fund_expire3 = isValidNumber(this.fundInfo.fund_expire[2]);
					if(this.fundInfo.fund_expire[1]&&!is_valid_fund_expire2){
						dialogConfirm("info", '基金期限输入格式不对');
						return false
					}
					if(this.fundInfo.fund_expire[2]&&!is_valid_fund_expire3){
						dialogConfirm("info", '基金期限输入格式不对');
						return false
					}
					if (is_valid_fund_expire1) {
						return true
					} else{
						dialogConfirm("info", '基金期限输入格式不对');
						return false
					}
				} else{
					dialogConfirm("info", '基金期限不能为空');
					return false
				}
			},
			// 获取备案状态
			getBeiAnStatus(){
				getBeiAnStatusApi({
					
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.allFilingStatus = res.data.payload.record_state;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 获取所有人员列表
			getAllUserList(){
				getUserlistApi({
					number: 10000,
					page: 1
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.allUserList = res.data.payload.data;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 获取子基金认购基金列表
			getSubscriptionFundList(){
				getSubscriptionFundListApi({
					id: this.id
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.allSubscriptionFundList[0].options = Object.assign({}, res.data.payload.fof_fund);
						this.allSubscriptionFundList[1].options = Object.assign({}, res.data.payload.self_fund);
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			cancelEdit(){
				this.reload()
//				this.fundInfo = Object.assign({}, this.fundInfoInit);
//				this.isDisabledEditable = true;
			},
			changeHideStatus(e){
				if (!this.isDisabledEditable) {
					if (e == 1) {
						this.isHide = 1;
					} else{
						this.isHide = 0;
					}
				}
			},
			
			
			
			changeEditStatus() {
				this.isDisabledEditable = false;
				this.$nextTick(function () {
		        		this.$refs.input1.$el.querySelector('input').focus();
		        });
			},
			handleClick(e) {
				//console.log(e);
			},
			edit(index) {
				//console.log(index);
				this.tableIndex = index;
			},
			save(row) {
				//console.log(row);
				this.tableIndex = -1;
			},
			changeShowStatus(e) {
				if(e == 'isInvestGroupShow') {
					this.isInvestGroupShow = !this.isInvestGroupShow;
				} else {
					this.isUnderFundShow = !this.isUnderFundShow;
				}
			},
			translateStringToArray(str){
				if (str) {
					var arr = str.split('+');
					return arr
				}
			},

			//导出企业信息
			downLoadCompanyInfo(){
				let key = window.localStorage.getItem("htcx_server_key");
				let href = url.sourceDataUrl + "/web/Company/CompanyInfoExcel?company_id=" + this.id + '&key=' + key + '&new_type=' + this.type;
				window.location.href = href;
			},

			//去基金运行
			toFundRunPage(){
				this.$router.push({
					name: "subFundRun",
					query: {
						id: this.id,
					}
				})
			},
		},
		computed: {
			
		}
	}
</script>

<style scoped="scoped">
	.blue_btn .icon{
		font-size: 33px !important;
	}
	.company {
		box-sizing: border-box;
		padding: 0 30px;
		font-size: 14px;
	}
	
	.company_header {
		color: #6292F4;
		border-bottom: 1px solid #EAEAEA;
	}
	
	.company_header span {
		display: inline-block;
		padding: 0 15px 10px 15px;
		border-bottom: 2px solid #6292F4;
	}
	
	.company_name {
		padding-bottom: 20px;
		border-bottom: 1px solid #EAEAEA;
	}
	
	.company_name_left {
		font-size: 16px;
		color: #33302D;
	}
	
	.company_name_left i {
		margin-right: 3px;
		color: #6292F4;
	}
	
	.company_name_right {
		margin-left: 80px;
		color: #6292F4;
	}
	
	.company_name_right i,
	.company_name_right span {
		color: #6292F4;
	}
	
	.company_name_right button {
		width: 115px;
		height: 30px;
		margin-left: 37px;
		color: white;
		font-size: 14px;
		border-radius: 20px;
		background: #6292F4;
	}
	/*公司信息表单模块*/
	.company_info {
		padding: 8px 5px 0 20px;
	}
	.form{
		flex-wrap: wrap;
		padding: 30px 0;
		color: #6F7071;
	}
	.form_list{
		display: inline-flex;
		margin-bottom: 19px;
	}
	.form_list_left{
		width: 156px;
		color: #6F7071;
	}
	.form_list_right{
		width: 260px;
    		height: 40px;
	}
	.radio_btn{
		width: 73px;
		height: 40px;
		margin-right: 38px;
		line-height: 40px;
		text-align: center;
		border-radius: 20px;
		background: #F3F6FB;
	}
	.radio_btn_active{
		color: white;
		background: #6292F4 !important;
	}
	.form_submit button{
		width: 94px;
		height: 36px;
		color: #6292F4;
		font-size: 14px;
		margin-left: 26px;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
	.form_submit button:nth-child(2){
		color: white;
		background: #6292F4;
	}
	/*工商信息等公司背景*/
	.company_background {
		margin-top: 60px;
	}
	.company_info_edit_btn{
		margin-top: 70px;
	}
	.company_info_edit_btn button{
		width: 94px;
		height: 36px;
		color: white;
		border-radius: 20px;
		background: #6292F4;
	}
	.company_info_edit_btn button:nth-child(1){
		margin-right: 28px;
	}
	/*股权穿透图开始*/
	
	.equity_map {
		margin-top: 50px;
	}
	
	.block_title {
		margin-bottom: 25px;
		padding-bottom: 10px;
		border-bottom: 1px solid #CECBC9;
	}
	
	.title_left>div {
		color: #6292F4;
		font-size: 16px;
	}
	
	.title_left .radio {
		width: 7px;
		height: 7px;
		margin-right: 12px;
		border-radius: 50%;
		background: #6292F4;
	}
	
	.title_right>div {
		color: #6292F4;
	}
	
	.title_right i {
		color: #6292F4;
	}
	
	.download {
		height: 30px;
		padding: 0 15px;
		margin-left: 35px;
		border: 1px solid #6292F4;
		border-radius: 20px;
	}
	
	.download span {
		color: #6292F4;
	}
	
	.download .iconfont {
		margin-right: 7px;
	}
	
	.switch_label {
		margin-right: 18px;
	}
</style>


